import { crudStore } from '@/shared/plugins/vuex'
import { paginationCrud } from '@/admin/store/cruds/table'
import { addressesBlacklistColumns, addressesBlacklist } from '@/admin/store/mock/blacklistsMock'

export default crudStore([
  paginationCrud({
    tableName: 'addressesBlacklist',
    totalCount: 123
  })
],
{
  namespaced: true,
  mutations: {
    ADD_ADDRESSES_BLACKLIST_ITEM (state, payload) {
      state.addressesBlacklist.data.push(payload)
    }
  },
  actions: {
    addAddressItem ({ commit }, payload) {
      commit('ADD_ADDRESSES_BLACKLIST_ITEM', payload)
    },
    setMockAddressesBlacklistData ({ commit }) {
      commit('SET_ADDRESSES_BLACKLIST_DATA', addressesBlacklist)
      commit('SET_ADDRESSES_BLACKLIST_COLUMNS', addressesBlacklistColumns)
    }
  },
  getters: {
    getVisibleAddressesBlacklistColumns (state) {
      return state.addressesBlacklist?.columns?.filter(el => el.isShow) || []
    }
  }
})
