<template>
  <section class="blacklists">
    <header class="blacklists__header">
      <Heading>{{ t('blacklists.title') }}</Heading>
    </header>
    <main class="blacklists__container">
      <Tabs
        v-model="activeTab"
        class="blacklists__container__tabs"
        :data="tabs"
        :inactive-opacity="0.7"
        @update:model-value="changeTab"
      >
        <template #default="{ item }">
          {{ item.title }}
        </template>
      </Tabs>
      <keep-alive>
        <component
          :is="activeTab.component"
        />
      </keep-alive>
    </main>
  </section>
</template>

<script>
import { computed, defineAsyncComponent, ref } from 'vue'
import { useI18n } from '@/shared/plugins/i18n'
import { useRoute, useRouter } from 'vue-router'
import { useModule } from '@/shared/plugins/vuex'
import { MODULES_NAMES } from '@/shared/common/modules'
import blacklistsModule from '@/admin/store/modules/blacklists'
import Heading from '@/admin/components/Heading'
import Tabs from '@/shared/components/UIKit/Tabs'

export default {
  name: 'Blacklists',
  components: {
    Tabs,
    Heading,
    IPsList: defineAsyncComponent(() => import('@/admin/views/settings/Blacklists/Tabs/IPsList')),
    AddressesList: defineAsyncComponent(() => import('@/admin/views/settings/Blacklists/Tabs/AddressesList'))
  },
  setup () {
    const router = useRouter()
    const route = useRoute()
    const { t } = useI18n()
    useModule(MODULES_NAMES.BLACKLISTS, blacklistsModule)

    const tabs = computed(() => [
      { title: t('blacklists.tabs.ipList'), key: 'ip', icon: 'ban', component: 'IPsList' },
      { title: t('blacklists.tabs.addressesList'), key: 'addresses', icon: 'ban', component: 'AddressesList' }
    ])
    const activeTab = ref(tabs.value.find(el => el.key === route.query.t) || tabs.value[1])

    const changeTab = (tab) => {
      activeTab.value = tab
      router.replace({ ...route, query: { t: tab.key } })
    }

    return {
      t,
      tabs,
      activeTab,
      changeTab
    }
  }
}
</script>

<style lang="scss">
.blacklists {
  height: 100%;
  padding: 30px 40px;
  display: flex;
  flex-direction: column;

  &__header {
    margin-bottom: 10px;
  }
  &__container {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
  }
}
</style>
